"use client";

import React, { useState } from "react";

const letters = [
  {
    title: "Letter 1",
    content:
      "From the moment you arrived in my life, it felt like a lyric out of 'Lover' — soft, sweet, and unforgettable.",
  },
  {
    title: "Letter 2",
    content:
      "Every day with you is a new verse in our beautiful song, filled with laughter, dreams, and endless love.",
  },
  {
    title: "Letter 3",
    content:
      "You are my heart’s melody, the rhythm that keeps me going, and the love I cherish forever.",
  },
];

export default function PersonalLetters() {
  const [openIndex, setOpenIndex] = useState<number | null>(null);

  const toggleLetter = (index: number) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  return (
    <div className="min-h-screen bg-pink-50 p-8 flex flex-col items-center text-gray-900">
      <h2 className="text-4xl font-serif mb-8">Personal Letters</h2>
      <div className="max-w-3xl w-full space-y-6">
        {letters.map((letter, idx) => (
          <div
            key={idx}
            className="bg-white rounded-lg shadow-md p-4 cursor-pointer select-none"
            onClick={() => toggleLetter(idx)}
          >
            <div className="font-semibold text-lg text-pink-700">{letter.title}</div>
            <div
              className={`mt-2 text-pink-900 transition-max-height duration-500 ease-in-out overflow-hidden ${
                openIndex === idx ? "max-h-96" : "max-h-0"
              }`}
            >
              <p>{letter.content}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
