"use client";

import React, { useState } from "react";
import BackgroundMusic from "../components/BackgroundMusic";
import HeartParticles from "../components/HeartParticles";
import Navigation from "./navigation";
import LandingPage from "./landing";
import Slideshow from "./slideshow";
import LoveNotes from "./love-notes";
import PersonalLetters from "./personal-letters";
import MemoryQuiz from "./memory-quiz";
import DigitalGiftBox from "./digital-gift-box";
import Goodbye from "./goodbye";

type Page =
  | "landing"
  | "slideshow"
  | "love-notes"
  | "personal-letters"
  | "memory-quiz"
  | "digital-gift-box"
  | "goodbye";

export default function App() {
  const [currentPage, setCurrentPage] = useState<Page>("landing");

  const renderPage = () => {
    switch (currentPage) {
      case "landing":
        return <LandingPage onStart={() => setCurrentPage("slideshow")} />;
      case "slideshow":
        return <Slideshow />;
      case "love-notes":
        return <LoveNotes />;
      case "personal-letters":
        return <PersonalLetters />;
      case "memory-quiz":
        return <MemoryQuiz />;
      case "digital-gift-box":
        return <DigitalGiftBox />;
      case "goodbye":
        return <Goodbye />;
      default:
        return <LandingPage onStart={() => setCurrentPage("slideshow")} />;
    }
  };

  return (
    <>
      <BackgroundMusic />
      <HeartParticles />
      {renderPage()}
      <Navigation currentPage={currentPage} onNavigate={setCurrentPage} />
    </>
  );
}
