"use client";

import React, { useState } from "react";

const questions = [
  {
    question: "Where did we first meet?",
    options: ["Coffee shop", "Library", "Park", "Museum"],
    answer: 0,
  },
  {
    question: "What’s our favorite song to dance to?",
    options: ["Lover", "You Belong With Me", "All Too Well", "Cardigan"],
    answer: 0,
  },
  {
    question: "Which city did we visit together first?",
    options: ["Paris", "New York", "San Francisco", "London"],
    answer: 2,
  },
  {
    question: "What’s the color of your favorite dress I love?",
    options: ["Red", "Blue", "Green", "Yellow"],
    answer: 1,
  },
  {
    question: "Which of these is NOT a memory we share?",
    options: [
      "Watching the sunset on the beach",
      "Riding a hot air balloon",
      "Dancing in the rain",
      "Skydiving together",
    ],
    answer: 3,
  },
];

export default function MemoryQuiz() {
  const [current, setCurrent] = useState(0);
  const [selected, setSelected] = useState<number | null>(null);
  const [score, setScore] = useState(0);
  const [showResult, setShowResult] = useState(false);

  const selectOption = (index: number) => {
    setSelected(index);
  };

  const nextQuestion = () => {
    if (selected === questions[current].answer) {
      setScore(score + 1);
    }
    setSelected(null);
    if (current + 1 < questions.length) {
      setCurrent(current + 1);
    } else {
      setShowResult(true);
    }
  };

  const getResultMessage = () => {
    if (score === questions.length) return "You’re my memory twin!";
    if (score >= questions.length / 2) return "Pretty impressive!";
    return "You better start paying more attention 😜";
  };

  return (
    <div className="min-h-screen bg-pink-50 p-8 flex flex-col items-center text-gray-900">
      <h2 className="text-4xl font-serif mb-8">Memory Quiz</h2>
      {!showResult ? (
        <div className="max-w-3xl w-full bg-white rounded-lg shadow-md p-6">
          <div className="mb-4 font-semibold text-lg">{questions[current].question}</div>
          <div className="space-y-3">
            {questions[current].options.map((option, idx) => (
              <button
                key={idx}
                onClick={() => selectOption(idx)}
                className={`w-full text-left p-3 rounded-md border ${
                  selected === idx ? "border-pink-500 bg-pink-100" : "border-gray-300"
                }`}
              >
                {option}
              </button>
            ))}
          </div>
          <button
            onClick={nextQuestion}
            disabled={selected === null}
            className="mt-6 bg-pink-400 hover:bg-pink-500 text-white font-semibold py-2 px-6 rounded-full shadow transition disabled:opacity-50"
          >
            Next
          </button>
        </div>
      ) : (
        <div className="max-w-3xl w-full bg-white rounded-lg shadow-md p-6 text-center text-xl italic font-semibold text-pink-700">
          {getResultMessage()}
        </div>
      )}
    </div>
  );
}
