"use client";

import React, { useState } from "react";

type Page =
  | "landing"
  | "slideshow"
  | "love-notes"
  | "personal-letters"
  | "memory-quiz"
  | "digital-gift-box"
  | "goodbye";

interface NavigationProps {
  currentPage: Page;
  onNavigate: (page: Page) => void;
}

export default function Navigation({ currentPage, onNavigate }: NavigationProps) {
  const pages: { id: Page; label: string }[] = [
    { id: "landing", label: "Start" },
    { id: "slideshow", label: "Our Moments" },
    { id: "love-notes", label: "Love Notes" },
    { id: "personal-letters", label: "Personal Letters" },
    { id: "memory-quiz", label: "Memory Quiz" },
    { id: "digital-gift-box", label: "Gift Box" },
    { id: "goodbye", label: "Goodbye" },
  ];

  return (
    <nav className="fixed bottom-4 left-1/2 transform -translate-x-1/2 bg-pink-100 bg-opacity-80 rounded-full shadow-lg px-4 py-2 flex space-x-4 z-20">
      {pages.map((page) => (
        <button
          key={page.id}
          onClick={() => onNavigate(page.id)}
          className={`text-sm font-semibold px-3 py-1 rounded-full transition ${
            currentPage === page.id
              ? "bg-pink-500 text-white shadow-md"
              : "text-pink-700 hover:bg-pink-300"
          }`}
        >
          {page.label}
        </button>
      ))}
    </nav>
  );
}
