"use client";

import React, { useState, useEffect } from "react";

export default function DigitalGiftBox() {
  const [revealed, setRevealed] = useState(false);
  const [heartbeat, setHeartbeat] = useState(false);

  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (!revealed) {
      interval = setInterval(() => {
        setHeartbeat((prev) => !prev);
      }, 600);
    } else {
      setHeartbeat(false);
    }
    return () => clearInterval(interval);
  }, [revealed]);

  return (
    <div className="min-h-screen bg-pink-50 p-8 flex flex-col items-center justify-center text-gray-900">
      <h2 className="text-4xl font-serif mb-8">Digital Gift Box</h2>
      <div
        className={`w-48 h-48 bg-pink-400 rounded-3xl shadow-lg flex items-center justify-center cursor-pointer select-none transition-transform ${
          heartbeat ? "scale-105" : "scale-100"
        }`}
        onClick={() => setRevealed(true)}
        aria-label="Reveal gift"
        role="button"
        tabIndex={0}
        onKeyDown={(e) => {
          if (e.key === "Enter" || e.key === " ") {
            setRevealed(true);
          }
        }}
      >
        {!revealed ? (
          <span className="text-white text-xl font-semibold">Reveal 🎁</span>
        ) : (
          <div className="text-white text-center p-4">
            <p className="mb-2 text-2xl font-bold">Surprise!</p>
            <p className="italic">A hidden gem just for you 💖</p>
            {/* Future: Add confetti or custom gift here */}
          </div>
        )}
      </div>
    </div>
  );
}
