"use client";

import React, { useState } from "react";

const media = [
  {
    type: "image",
    src: "/photo1.jpg",
    caption: "You don’t even know what this moment does to me.",
  },
  {
    type: "image",
    src: "/photo2.jpg",
    caption: "Our laughter echoes in every frame.",
  },
  {
    type: "image",
    src: "/photo3.jpg",
    caption: "Every glance, a lyric in our love song.",
  },
  {
    type: "image",
    src: "/photo4.jpg",
    caption: "Soft whispers and stolen kisses.",
  },
  {
    type: "image",
    src: "/photo5.jpg",
    caption: "Forever starts with you and me.",
  },
  {
    type: "video",
    src: "/video1.mp4",
    caption: "You make my heart skip a beat.",
  },
  {
    type: "video",
    src: "/video2.mp4",
    caption: "Dancing through life with you.",
  },
  {
    type: "video",
    src: "/video3.mp4",
    caption: "Our story in every frame.",
  },
  {
    type: "video",
    src: "/video4.mp4",
    caption: "Moments that take my breath away.",
  },
  {
    type: "video",
    src: "/video5.mp4",
    caption: "You’re my favorite melody.",
  },
];

export default function Slideshow() {
  const [index, setIndex] = useState(0);

  const next = () => {
    setIndex((prev) => (prev + 1) % media.length);
  };

  const current = media[index];

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-pink-50 p-6 text-gray-900 relative">
      <div className="max-w-3xl w-full rounded-lg shadow-lg bg-white p-6 flex flex-col items-center">
        {current.type === "image" ? (
          <img
            src={current.src}
            alt={current.caption}
            className="rounded-md max-h-[400px] object-contain mb-4 transition-opacity duration-700 ease-in-out"
            key={current.src}
          />
        ) : (
          <video
            src={current.src}
            controls
            className="rounded-md max-h-[400px] mb-4 transition-opacity duration-700 ease-in-out"
            key={current.src}
          />
        )}
        <p className="text-center text-lg italic mb-6">{current.caption}</p>
        <button
          onClick={next}
          className="bg-pink-400 hover:bg-pink-500 text-white font-semibold py-2 px-6 rounded-full shadow transition"
        >
          Next ❤️
        </button>
      </div>
    </div>
  );
}
